import type { Dispatch, SetStateAction } from 'react';

// 1. page.tsx  props   . ( )
export interface SidebarProps {
  activeTab: 'panel' | 'doctor';
  activeMenu: string;
  onMenuClick: Dispatch<SetStateAction<string>>; //  (menu: string) => void;
}

// 2.    .
const panelMenus = [
  { id: 'dashboard', name: '', icon: 'ri-dashboard-line' },
  { id: 'query', name: ' ', icon: 'ri-edit-box-line' },
  { id: 'results', name: '/', icon: 'ri-file-chart-line' },
];

const doctorMenus = [
  { id: 'dashboard', name: 'AI  ', icon: 'ri-home-heart-line' },
  { id: 'query', name: ' ', icon: 'ri-chat-3-line' },
  { id: 'results', name: ' ', icon: 'ri-medicine-bottle-line' },
  { id: 'settings', name: '', icon: 'ri-settings-3-line' },
];

export default function Sidebar({ activeTab, activeMenu, onMenuClick }: SidebarProps) {
  
  // 3. activeTab    .
  const menus = activeTab === 'panel' ? panelMenus : doctorMenus;

  const getMenuClass = (menuId: string) => {
    return activeMenu === menuId
      ? "bg-[#2F6BFF]/10 text-[#2F6BFF] font-medium" //  
      : "text-gray-600 hover:bg-gray-100 hover:text-gray-900"; //  
  };

  return (
    <aside className="w-64 bg-white border-r border-gray-200 flex-shrink-0 p-4">
      <nav className="flex flex-col space-y-2">
        {/* 4.  menus  map . */}
        {menus.map((menu) => (
          <button
            key={menu.id}
            className={`flex items-center gap-3 px-4 py-2.5 rounded-lg text-sm transition-colors ${getMenuClass(menu.id)}`}
            onClick={() => onMenuClick(menu.id)}
          >
            <i className={`${menu.icon} text-lg`}></i>
            <span>{menu.name}</span>
          </button>
        ))}
      </nav>
    </aside>
  );
}
