// 1. React import .
import React from 'react';
// 2. 'ReactNode'  'import type' .
import type { ReactNode } from 'react';

// 3. () CardProps div   (onClick ) 
//    React.HTMLAttributes<HTMLDivElement> extends .
interface CardProps extends React.HTMLAttributes<HTMLDivElement> {
  children: ReactNode;
  className?: string;
  padding?: 'sm' | 'md' | 'lg';
}

export default function Card({ 
  children, 
  className = '', 
  padding = 'md',
  ...props // 4. ()   (onClick ) props .
}: CardProps) {
  
  const paddingClasses = {
    sm: 'p-4',
    md: 'p-6',
    lg: 'p-8'
  };
  
  return (
    // 5. () ...props div  onClick   .
    <div 
      className={`bg-white rounded-xl shadow-sm border border-gray-100 ${paddingClasses[padding]} ${className}`}
      {...props} 
    >
      {children}
    </div>
  );
}

